

/* global */
*{box-sizing:border-box}
body{
  margin:0;
  font-family: "Poppins", sans-serif;
  background:var(--bg);
  color:var(--text);
  transition: background .22s ease, color .22s ease;
}
a{ color:inherit; text-decoration:none; }


:root {
  /* light defaults */
  --bg: #f0f4f8;          
  --text: #1e3a8a;         
  --nav-bg: #e0e7ff;       
  --menu-bg: #ffffff;      
  --accent: #3b82f6;       
  --accent-hover: #2563eb;
  --btn-color: #ffffff;   
  --hover: rgba(59,130,246,0.1); 
  --dropdown-border: rgba(59,130,246,0.2);
}








